.carousel {

  // Core

  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;

  &--loaded {
    display: block;
  }

  &--loading {
    opacity: 0;
    display: block;
  }

  &--hidden {
    opacity: 0;
  }

  &--refresh {
    .carousel__item {
      display: none;
    }
  }

  &--drag {
    .carousel__item {
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }
  }

  &--grab {
    cursor: move;
    cursor: -webkit-grab;
    cursor: -o-grab;
    cursor: -ms-grab;
    cursor: grab;
  }
  
  &--rtl {
    direction: rtl;
    &__item {
      float: right;
    }
  }


  &__stage {
    position: relative;
    -ms-touch-action: pan-Y;
    &:after {
      content: ".";
      display: block;
      clear: both;
      visibility: hidden;
      line-height: 0;
      height: 0;
    }
  }

  &__wrapper {
    position: relative;
    overflow: hidden;
    /* fix for flashing background */
    -webkit-transform: translate3d(0px, 0px, 0px);
  }

  &__item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    img {
      display: block;
      width: 100%;
      -webkit-transform-style: preserve-3d;
    }
  }

  &__navigation.disabled,
  &__dots.disabled {
    display: none;
  }

  &__navigation .carousel__prev,
  &__navigation .carousel__next,
  &__dot {
    cursor: pointer;
    cursor: hand;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }


  // Styling dots
  &__navigation.disabled + &__dots {
    margin-top: 10px;
  }
  &__dots {
    text-align: center;
    -webkit-tap-highlight-color: transparent;
  }
  &__dot {
    display: inline-block;
    zoom: 1;
    *display: inline;

    span {
      width: 10px;
      height: 10px;
      margin: 5px 7px;
      background: $brand-secondary;
      display: block;
      -webkit-backface-visibility: visible;
      //@include transition(opacity, 200ms, ease);
      border-radius: 100%;
    }

    &.active,
    &:hover {
      span {
        background: lighten($brand-secondary, 30%);
      }
    }
  }


  // Custom Theme

  &--gallery {
    @include make-sm-column(8);
    @include make-sm-column-offset(3);
    @include make-sm-column-pull(1);
    margin-bottom: 2rem;

    figure {
      border: 1px solid $brand-secondary;
      position: relative;
      width: 100%;
      img {
        max-width: 100%;
        width: 100%;
      }
      figcaption {
        position: absolute;
        bottom: 0;
        width: 100%;
        margin: 0;
        padding: 1rem;
        font-size: 1.125rem;
        color: #000;
        background: rgba(#f9f9f9, .9);
      }
    }

    .carousel__next,
    .carousel__prev {
      @include align('vertical');
      margin: 0;
      background-color: #fff;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: 18px 33px;
      transition: .2s linear;
      width: 3.5rem;
      height: 3.5rem;
      text-indent: -9999px;
      cursor: pointer;
      &:hover {
        background-color: $brand-secondary;
      }
    }
    .carousel__next {
      right: -3.5rem;
      background-image: url("../images/gallery-next.png");
      &:hover {
        background-image: url("../images/gallery-next-hover.png");
      }
    }
    .carousel__prev {
      left: -3.5rem;
      background-image: url("../images/gallery-prev.png");
      &:hover {
        background-image: url("../images/gallery-prev-hover.png");
      }
    }
  }

  &--clients,
  &--partners {
    @include make-xs-column(10);
    @include make-xs-column-push(1);



    .carousel__prev,
    .carousel__next {
      @include align('vertical');
      margin: 0;
      width: 2rem;
      height: 2rem;
      transition: all .2s ease;
      text-indent: -9999px;
      cursor: pointer;
      background-color: transparent;
      background-size: contain;
      background-position:  50% 50%;
      background-repeat: no-repeat;

      @include breakpoint($screen-sm) {
        width: 2.5rem;
        height: 2.5rem;
      }
    }
    .carousel__next {
      right: -2.5rem;
      background-image: url("../images/next.png");
      &:hover {
        background-image: url("../images/next-hover.png");
      }
    }
    .carousel__prev {
      left: -2.5rem;
      background-image: url("../images/prev.png");
      &:hover {
        background-image: url("../images/prev-hover.png");
      }
    }
  }

  &--slider {
    position: relative;
    margin-bottom: 4.5rem;
    //overflow: hidden;

    .carousel__wrapper {
      margin-bottom: -3rem;
    }

    .carousel__stage {
      padding-bottom: 3rem;
    }

    .carousel__next,
    .carousel__prev {
      position: absolute;
      top: 0;
      bottom: 3rem; // .carousel__caption height
      //height: 100%;
      margin: 0;
      cursor: pointer;
      padding: 0;
      width: 3.5rem;
      transition: .2s ease-in-out;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-color: rgba($brand-secondary, .3);
      text-indent: -9999px;
      &:hover {
        background-color: rgba($brand-secondary, .7);
      }
    }
    .carousel__next {
      right: 0;
      background-image: url("../images/gallery-next-hover.png");
    }
    .carousel__prev {
      left: 0;
      background-image: url("../images/gallery-prev-hover.png");
    }

    .carousel__image {
      overflow: visible;
      position: relative;
      a {
        display: block;
      }
      img {
        width: 100%;
        max-width: 100%;
      }
    }
    .carousel__caption {
      height: 3rem;
      line-height: 3rem;
      background: #e0e0e0;
      text-align: center;
      position: absolute;
      bottom: -3rem;
      width: 100%;
    }
  }
}