html {
  font-size: 70%;
  @include breakpoint($screen-xs) {
    font-size: 100%;
  }

}
svg {
  //fill: transparent;
  //fill: #fff;
  //stroke: rgba(#fff, .5%);
  //fill-opacity: 0;
  //stroke: #fff;
  ////stroke-width: 1px;
  //stroke-linecap: round;
  //stroke-linejoin: round;

  path {
    fill: transparent;
    stroke: white;
    stroke-width: 1px;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: path 5s alternate infinite;
  }


}

body {
  @include clearfix;
  -webkit-font-smoothing: antialiased;

  //display: none;
}
.disable-hover,
.disable-hover * {
  pointer-events: none !important;
}
@mixin align($direction: both) {
  position: absolute;
  transform-style: preserve-3d;

  @if index("horizontal" "h", $direction) {
    left: 50%;
    transform: translateX(-50%);
  } @else if index("vertical" "v", $direction) {
    top: 50%;
    transform: translateY(-50%);
  } @else {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

%title {
  color: #2d3037;
  font-size: 1.875rem;
  font-weight: 600;
  //line-height:.8;
  text-transform: uppercase;
  margin-top: 0;
}
%hr {
  background: $brand-primary;
  border: none;
  height: 10px;
  height: .625rem;
  width: 55px;
  width: 3.4325rem;
  text-align: left;
  display: inline-block;
  margin-top: 0;
  margin-bottom: 3rem;
}
%post-header {
  text-align: center;
  //margin-bottom: 3rem;
  height: 400px;
  height: 25rem;
  width: 100%;
  position: relative;
  background: transparent;
}
%post-title {
  @extend .container-fluid;
  height: 25rem;

  h1 {
    @include align();
    width: 80%;
    font-size: 2.5rem;
    font-weight: 700;
    color: #fff !important;
    text-transform: uppercase;
    margin: 0;
  }
}
body.home {
  background-color: $brand-secondary;
