.project {
  @include breakpoint(min-width $screen-xs) {
    @include make-xs-column(6);
  }
  @include make-md-column(3);
  display: block;
  margin-bottom: 2rem;


  &__caption {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(55,58,65, .8);
    min-height: 3.5rem;
    padding: .5rem;
    text-align: center;
    font-size: 14px;
    //transition: .2s linear;
    transition: all 0.2s ease;
  }

  &__image {
    position: relative;
    border: 1px solid $brand-primary;
    transition: all 0.2s ease;
    img {
      opacity: .5;
      max-width: 100%;
      width: 100%;
      //transition: .2s linear;
      transition: all 0.2s ease;
    }
  }

  &:hover {
    .project__image {
      z-index:1000;
      box-shadow:rgba(0, 0, 0, 0.3) 0 16px 16px 0;
    }

    img {
      opacity: 1;
    }
    .project__caption {
      background: $brand-primary;
    }
  }

}