.form {
  @include clearfix;

  &__left,
  &__right {
    @include make-sm-column(6);
  }
  &__textarea {
    //height: 10rem;
    resize: none;
  }
  &__control {
    @extend .form-control;
    font-family: $font-family-base;
    font-size: 1.125rem;
    font-weight: 300;
  }
}
.ajax-loader {
  display: none;
}