.page {

  &--service {
    .feedback {
      background: #f1f1f1 url("../images/bg-feedback-inner.png") no-repeat center center;
    }
    .photo-gallery {
      background: none;
      .button {
        display: none;
      }
    }
  }

  &__type {
    padding: 1.5rem 0;
    border-bottom: none;
    color: #fff;
    font-size: 48px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    background: $brand-secondary url("../images/bg-page-type.jpg") no-repeat;
    h1 {
      margin: 1rem 0;
    }
  }
  &__body {
    //@extend .row;
    //padding-top: 3.5rem;
    //padding-bottom: 3.5rem;
    @include clearfix;
  }
  &__header {
    @extend %post-header;

    &--service {
      @include breakpoint($screen-xs) {
        height: 18.75rem;
      }
    }
  }
  &__thumbnail {
    display: none;
    @include make-sm-column(3);
    @include align(v);
    @include breakpoint($screen-sm) {
      display: block;
    }
  }
  &__description {
    @include make-sm-column(9);
    @include make-sm-column-offset(3);
    @include align(v);
    text-align: left;
    color: #ff5f3d;
    font-size: 0.875rem;
    font-weight: 300;
    h2 {
      color: #e0e0e0;
      font-size: 3rem;
      font-weight: 300;
      margin-top: 0;
      margin-bottom: 1.5rem;
    }
    ul {
      padding-left: 0;
      list-style-position: inside;
    }
  }
  
  &__title {
    @extend %post-title;
  }
  &__content {
    //padding: 3.5rem 0;
    padding-top: 3.5rem;
    background: #fff;
    //background: #fff url("../images/bg-polygon.png");
  }
  &__post {
    @extend .container-fluid;
    @extend %typography;
  }
  &__footer {
    text-align: center;
    padding-bottom: 3.5rem;
    @extend .container-fluid;
    hr {
      margin-top: 0;
      margin-bottom: 2rem;
    }
  }
}