%typography {
  font-size: 1rem;
  line-height: 1.5rem;
  color: #000;

  hr {
    margin: 3rem 0;
  }
  p {
    margin-bottom: 1.5rem;
    font-weight: 400;
  }
  strong {
    font-weight: 600;
  }
  h1, h2, h3, h4, h5, h6 {
    text-transform: uppercase;
    color: $brand-secondary;
    //margin: 2rem 0;
    margin-top: 0;
    margin-bottom: 1.5rem;
  }
  h1 {
    font-size: 3.157rem;
  }
  h2 {
    font-size: 2.369rem;
  }
  h3 {
    font-size: 1.777rem;
  }
  h4 {
    font-size: 1.333rem;
    color: $brand-primary;
  }
  h5 {
    font-size: 1rem;
    color: $brand-primary;
  }
  h6 {
    font-size: .75rem;
  }
  img, iframe, object {
    max-width: 100%;
    height: auto;
  }

  ul, ol {
    margin-bottom: 1.5rem;
    //li {
    //  margin-bottom: 1rem;
    //}
  }

  blockquote {
    background: $brand-primary;
    color: #fff;
    font-weight: 700;
    border: none;
    padding: 3rem;
    margin: 3rem 0;
    h3 {
      margin-top: 0;
      color: #fff;
    }
  }

  table {
    @extend .table;
    @extend .table-bordered;
    background: $brand-secondary;
    overflow: hidden;
    outline: none;
    text-align: center;
    color: #fff;
    border-radius: 6px;
    border-color: transparent;
    font-size: 12px;

    tbody {
      border-color: transparent;
    }

    td {
      border-color: #72757e !important;
    }

    tr {
      &:first-of-type {
        color: #ff9781;
        border-top: none;

        td {
          border-top: none;
          &:last-of-type {
            border-right-color: transparent;
          }
        }
      }
      &:last-of-type {
        border: none;
        td {
          border-bottom: none;
          &:last-of-type {
            border-right-color: transparent;
          }
        }
      }
    }

    td[rowspan] {
      background: $brand-primary;
      //background-size: contain;
      background-origin: border-box;
      color: #fff;
      vertical-align: middle;
      width: 80px;
      text-align: center;
      border-color: transparent;

      .vertical-text {
        display: inline-block;
        overflow: hidden;

        width: 1.5em;
        line-height: 1.5;
        span {
          display: inline-block;
          white-space: nowrap;

          transform: translate(0,100%) rotate(-90deg);
          transform-origin: 0 0;

          &:after {
            content: "";
            float: left;
            margin-top: 100%;
          }
        }
      }
      p {
        transform: translate(0,300%) rotate(-90deg);
        transform-origin: top 0;
        float: right;
        margin: 0;
        white-space: nowrap;
        height: auto;
        width: 80px;
        //display: inline-block;
      }
    }
  }
}
.table-responsive {
  border-radius: 6px;
  border: none;
  outline: none;
}