.contacts {
  background-color: #ccc;
  position: relative;
  height: 495px;
  text-align: left;


  &__title {
    background: #fff;
    padding: 1rem 1.5rem;
    margin-top: 3rem;
    display: inline-block;
    box-shadow: -2.5px 4.3px 5px rgba(0, 1, 1, 0.2);
    z-index: 2;
    position: relative;

    h1 {
      @extend %title;
    }
    hr {
      @extend %hr;
      margin-bottom: 0;
    }
  }

}

.info {
  @extend .row;
  z-index: 2;
  background: $brand-secondary;
  color: #fff;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.5);
  font-size: 1.25rem;
  font-weight: 300;
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 1.5rem;

  @include breakpoint($screen-sm) {
    border-radius: 11px 11px 0 0;
  }



  a {
    @include make-md-column(4);
    color: #fff;
    padding-left: 2.5rem;
    margin-bottom: .5rem;
    line-height: 2rem;
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }
    &:before {
      content: '';
      width: 2rem;
      height: 2rem;
      left: 0;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: contain;
      @include align('vertical');
    }

    //@include breakpoint($screen-sm) {
    //  background: $brand-primary;
    //  color: #000 !important;
    //}
    @include breakpoint($screen-md) {
      margin-bottom: 0;
    }
  }

  &-phone {
    @include make-xs-column(12);
    &:before {
      background-size: 18px 32px;
      background-image: url('data:image/png;base64,');
    }
  }

  &-email {
    @include make-xs-column(12);
    &:before {
      background-size: 32px 22px;
      background-image: url('data:image/png;base64,');
    }
  }
  &-address {
    @include make-xs-column(12);
    &:before {
      background-size: 24px 32px;
      background-image: url('data:image/png;base64,');
    }
  }
}
