.alert {
  background: #fff;
  border-radius: 0;
  margin: 0;
  padding-bottom: 3.5rem;
  &__body {
    text-align: center;
    p {
      font-size: 1.5rem;
      text-transform: uppercase;
      font-weight: 400;
      border: 3px solid $brand-primary;
      display: inline-block;
      padding: .5rem 2rem;
      margin: 0;
    }
  }
}