.about {
  padding: 3.5rem 0;
  background-color: #fff;
  text-align: left;
  
  &__title {
    @extend %title;
  }
  hr {
    @extend %hr;
  }

  &__text {
    vertical-align: top;
    p {
      display: inline-block;
      margin-bottom: 1rem;
      font-size: 1.125rem;
      color: #2d3037;
    }
  }

  &__attention {
    text-align: center;
    margin-top: 3.5rem;
    p {
      font-size: 1.5rem;
      text-transform: uppercase;
      font-weight: 400;
      border: 3px solid $brand-primary;
      display: inline-block;
      padding: .5rem 2rem;
    }
  }

  @include breakpoint($screen-sm) {
    &__text {
      column-count: 2;
      column-gap: 50px;
    }
  }
}