.footer {
  //@include clearfix;
  //padding-top: 2.5rem;
  height: 100px;
  font-weight: 200;
  background: $brand-secondary url("../images/bg-footer.png") no-repeat 50% 95%;
  text-align: center;
  color: #7e8492;
  &__copyright {
    @include make-sm-column(6);
    //float: left;
    //text-align: left;
    padding-top: 2.5rem;
    @include breakpoint($screen-sm) {
      text-align: left;
    }
  }
  &__design {
    @include make-sm-column(6);
    padding-top: 2.5rem;
    @include breakpoint($screen-sm) {
      text-align: right;
    }
  }
}

.scroll-top {
  width: 48px;
  height: 48px;
  display: block;
  background: url("../images/top.svg") no-repeat 50% 50%;
  background-size: 48px 48px;
  @include align();
}