.service {
  @include breakpoint(min-width $screen-xs) {
    @include make-xs-column(6);
  }
  @include make-md-column(3);
  transition: all .2s ease;
  text-align: center;
  margin-bottom: 2rem;

  @include breakpoint($screen-sm) {
    &:nth-of-type(odd) {
      clear: both;
    }
  }
  @include breakpoint($screen-md) {
    &:nth-of-type(odd) {
      clear: none;
    }
  }


  &__link {
    display: block;
    width: 100%;
    padding-top: 220px;
    &:hover {
      .service__title {
        background: $brand-primary;
        color: #fff;
        z-index:1000;
        box-shadow:rgba(0, 0, 0, 0.3) 0 16px 16px 0;
      }
    }
  }

  &__title {
    display: inline-block;
    font-weight: 100;
    //font-size: 1.25rem;
    font-size: 16px;
    border: 2px solid $brand-primary;
    padding: .5rem;
    transition: .2s ease-in;
    color: #fff;
  }

  &__description {
    margin-top: 1rem;
    color: #c2c2c2;
    //font-size: .875rem;
    font-size: 14px;
    font-weight: 300;
    text-align: left;
    ul {
      padding-left: 0;
      li {
        position: relative;
        margin-bottom: 1rem;
        padding-left: 1rem;
        list-style: none;
        &:before {
          content: '•';
          position: absolute;
          left: 0;
          //top: -.375rem;
          //font-size: 1.5rem;
          top: -2px;
          font-size: 18px;
          color: $brand-primary;
        }
      }
    }
  }
  background-position: 50% 0;
  background-repeat: no-repeat;

  svg, img {
    //position: absolute;
    @include align(h);
  }
}