%button {
  @extend .btn;
  @extend .btn-lg;
  @extend .btn-primary;

  text-transform: uppercase;
  font-size: 14px;
  font-size: .875rem;
  font-weight: 600;
  padding: 13px 40px;
  padding: .8125rem 2.5rem;
  transition: .2s linear;
  background-repeat: no-repeat;
  outline: none;

  &:hover {
    background-color: #ef431f;
    border-color: #ff5f3d;
    background-color: rgba($brand-primary, .55);
    box-shadow:rgba(0, 0, 0, 0.3) 0 16px 16px 0;
  }
}
%button-rounded {
  border-radius: 25px;
}
%button-arrow-right {
  background-image: url('data:image/png;base64,');
  background-size: 6px 10px;
  background-size: .375rem .625rem;
  background-position: 85% 50%;
  &:hover {
    background-position: 90% 50%;
  }
}
%button-arrow-left {
  background-image: url('data:image/png;base64,');
  background-size: 6px 10px;
  background-size: .375rem .625rem;
  background-position: 15% 50%;
  &:hover {
    background-position: 10% 50%;
  }
}
%button-more {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    right: 15px;
    right: .9375rem;
    top: 13px;
    top: .8125rem;
    width: 14px;
    width: .875rem;
    height: 14px;
    height: .875rem;
    background: url("../images/loading.svg") no-repeat 50% 50%;
    background-size: contain;
  }
  &.loading {
    &:after {
      animation: loading .8s infinite linear;
    }
  }
  /* Animation */
  @keyframes loading {
    from { }
    to { transform: rotate(360deg); }
  }
}

.button {
  @extend %button;
  margin-top: 1.5rem;
  &--gray {
    background-color: #3d414a;
    border: 3px solid $brand-secondary;
    &:hover {
      background-color: $brand-secondary;
      border-color: #3d414a;
    }
  }
  &--orange {
    border: 3px solid #ef431f;
    &:hover {
      background-color: #ef431f;
      border-color: #ff5f3d;
    }
  }
  &--white {
    //font-size: 0.875rem;
    //padding: 12px 1.5rem;
    background: transparent;
    border: 3px solid #fff;
    &:hover {
      background-color: #fff;
      color: $brand-primary;
      border-color: #fff;
    }
  }
  &--rounded {
    @extend %button-rounded;
  }
  &--right {
    @extend %button-arrow-right;
  }
  &--left {
    @extend %button-arrow-left;
  }
  &--jumbotron {
    margin-top: 3.5rem;
    background-color: transparent;
    border-color: #fff;
    border-width: 2px;
    padding-left: 2rem;
    background-position: 90% 50%;
    background-size: 15px 16px;
    background-image: url('data:image/png;base64,');
    &:hover {
      background-color: #fff;
      color: $brand-primary;
      border-color: #fff;
      background-image: url('data:image/png;base64,');
    }
  }

  &--more {
    @extend %button-more;
  }
}


