.text {
  @include clearfix;
  padding: 3.5rem 0;
  //background: #fff;

  &:first-of-type {
    padding-top: 0;
  }

  &--underlined {
    .text__body {
      border-bottom: 1px solid $brand-primary;
      padding-bottom: 2rem;
    }
  }

  &__body {
    @extend .container-fluid;
  }
  &__row {
    @extend .row;
  }

  &__title {
    @include make-xs-column(12);
    text-align: left;
    h2 {
      @extend %title;
    }
    hr {
      @extend %hr;
    }
  }

  &__content {
    @include make-xs-column(12);
    @extend %typography;

    &--left,
    &--right {
      @include make-sm-column(6);
    }
  }
}