.video {
  //@include make-sm-column(6);
  @include breakpoint(min-width $screen-xs) {
    @include make-xs-column(6);
  }
  @include make-sm-column(4);
  margin-bottom: 2rem;

  &__thumbnail {
    position: relative;
    transition: all .2s ease;
    img {
      max-width: 100%;
      width: 100%;
    }
    span {
      display: none; // Savefrom hide
    }
    &:hover {
      //z-index:1000;
      box-shadow:rgba(0, 0, 0, 0.3) 0 16px 16px 0;
    }
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    text-decoration: none;
    transition: all .2s ease;
    text-align: center;
    color: #fff;

    &:hover {
      background-color: rgba(255, 95, 61, .5);
    }

    &:after {
      content: '';
      display: block;
      width: 100px;
      height: 100px;
      font-size: 48px;
      padding-left: 10px;
      line-height:100px;
      color: #fff !important;
      -webkit-appearance: none;
      -moz-appearance: none;
      border-radius: 100%;
      background-color: rgba(45, 48, 55, .55);
      background-image: url("../images/play.svg");
      background-repeat: no-repeat;
      background-position: 60% 50%;
      @include align();
      z-index: 20;
    }
  }
}