.jumbotron {
  //height: 450px;
  //min-height: 28.125rem;
  margin-bottom: 0;
  text-align: left;
  background: url("../images/bg-jumbotron.jpg") no-repeat top center;
  background-size: cover;
  position: relative;

  &__title {
    text-transform: uppercase;
    font-weight: 700;
    word-wrap: break-word;
    margin-bottom: 0;
  }
  &__body {
    z-index: 1000;
  }
  p {
    font-size: 1.125rem;
  }
}