.feedback {
  padding: 3.5rem 0;
  text-align: center;
  z-index: 10;
  position: relative;
  background-color: #fff;

  @include breakpoint($screen-md) {
    background-color: #fff;
    background: url("../images/bg-feedback.png") no-repeat top center;
  }

  &__title {
    text-align: left;
    h2 {
      @extend %title;
    }
    hr {
      @extend %hr;
    }
  }

  &__subtitle {
    text-align: center;
    h2 {
      font-size: 1.875rem;
      font-weight: 400;
      margin-top: 0;
      margin-bottom: 3.5rem;
    }
  }
  form p {
    margin-bottom: 0;
  }
}