.discover {
  padding: 3.5rem 0;
  background: #fafafa url("../images/bg-discover.png") no-repeat 50% 50%;

  &__body {
    @extend .container-fluid;
  }
  &__image {
    z-index: 10;
    text-align: center;
    img {
      max-width: 100%;
    }

    @include breakpoint($screen-sm) {
      @include align(v);
      max-width: 325px;
    }
  }
  &__title {
    font-size: 1.875rem;
    font-weight: 300;
    text-transform: uppercase;
    margin-top: 1.5rem;
  }
  &__content {
    text-align: center;
    
    @include breakpoint($screen-sm) {
      text-align: left;
      background: #fafafa url("../images/bg-discover.png") no-repeat 50% 50%;
      min-height: 240px;
      width: 530px;
      margin-left: 200px;
      border-radius: 4px;
      padding-left: 180px;
      padding-top: .5rem;
      box-shadow: 0 1px 1px rgba(1, 2, 2, 0.3), inset 0 1px 0 white;
    }
  }

  @include breakpoint($screen-sm) {
    background: none;
  }
}