.navbar {
  box-shadow: 1px 0 10px rgba(0,0,0, .3);
  transition: all .3s;

  &--default {
    @extend .navbar-default;
  }

  &--fixed-top {
    @extend .navbar-fixed-top;
  }

  &--right {
    @extend .navbar-right;
  }

  &__body {
    @extend .container-fluid;
  }

  &__header {
    @extend .navbar-header;
    height: $navbar-height;
  }

  &__collapse {
    @extend .navbar-collapse;
    background: #fff;
    width: 100%;
    margin: 0;
    //li>a {
    //  padding: 0 15px !important;
    //}
  }
  &__brand {
    @extend .navbar-brand;
    position: relative;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    display: table;

    div {
      display: table-cell;
      vertical-align: middle;
      height: 2.875rem !important;
      width: 2.375rem !important;
    }
    img {
      height: 2.875rem;
      width: 2.375rem;
      display: block;
    }
    h1 {
      display: table-cell;
      vertical-align: middle;
      //@include align('vertical');
      padding-left: 1rem;
      //color: $brand-secondary;
      color: #343b55;
      font-size: 1.375rem;
      font-weight: 700;
      line-height: 1.375rem;
      margin: 0;
      //text-transform: uppercase;

      small {
        text-transform: none;
        color: #7b8394;
        font-size: 0.8125rem;
        font-weight: 400;
      }
    }
  }

  &__menu {
    @extend .navbar-nav;
    //@extend .nav;
    //&--right {
    //  float: right;
    //}
  }

  &__toggle {
    @extend .navbar-toggle;
    transition: .2s linear;
    //margin: 0;
    //padding: 0;
    //&:hover {
    //  .icon-bar {
    //    background-color: #fff;
    //  }
    //}
  }

  li {
    position: relative;
  }

  @include breakpoint($screen-sm) {
    height: $navbar-height-full;

    &__menu {
      &--right {
        float: right;
      }
    }

    li {
      &:before {
        content: '';
        position: absolute;
        height: 5px;
        top: -5px;
        left: 5%;
        right: 5%;
        transition: .2s linear;
      }
      &.active:before {
        background: $brand-primary;
        top: 0;
      }

      > a {
        padding: (($navbar-height-full - $line-height-computed) / 2) $navbar-padding-horizontal;
      }
    }

    &-toggle {
      margin-top: (($navbar-height-full - 34px) / 2);
      margin-bottom: (($navbar-height-full - -34px) / 2);
    }

    &__brand {
      padding: (($navbar-height-full - $line-height-computed) / 2) $navbar-padding-horizontal;
      height: $navbar-height-full;
      img {
        width: auto;
      }
      h1 {
        padding-left: 20px;
        font-size: 1.325rem;
      }
    }
  }
}