.photo-gallery {
  padding: 3.5rem 0;
  background: $brand-primary url("../images/bg-gallery.jpg") no-repeat center top;
  text-align: center;

  &__title {
    text-align: left;
    h1 {
      @extend %title;
      color: #fff;
    }
    hr {
      @extend %hr;
      background: $brand-secondary;
    }
  }
}