.article {
  @include make-sm-column(6);
  @include make-md-column(4);
  clip-path: polygon(0 100%, 100% 100%, 100% 18%, 87% 0, 0 0);
  margin-bottom: 2rem;

  &__box {
    clip-path: polygon(0 100%, 100% 100%, 100% 14%, 90% 0, 0 0);
    position: relative;
  }

  img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: inline-block;
  }

  &__link {
    clip-path: polygon(0 100%, 100% 100%, 100% 14%, 90% 0, 0 0);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 5;
    transition: all .2s ease;
  }

  &__title {
    @include align();
    background-color: rgba($brand-secondary, .85);
    box-shadow: -4px 4px 0 rgba(0, 0, 0, 0.2);
    padding: 2rem;
    font-size: 20px;
    font-weight: 300;
    margin: 0;
    color: #fff;
    width: 80%;
    text-align: center;
    z-index: 10;
    transition: all .2s ease;
  }

  &:hover {
    .article__link {
      background-color: rgba($brand-primary, .55);
      box-shadow:rgba(0, 0, 0, 0.3) 0 16px 16px 0;
    }
    .article__title {
      box-shadow: -8px 8px 0 rgba(0, 0, 0, 0.2);
    }
  }

}